<template>
    <div>
        <section style="margin: 35px 0px;">
            <!-- Render loading -->
            <div v-if="loading">
               <!-- <h3>Loading...</h3> -->

               <!-- Credit to Raubaca -->
               <!-- https://codepen.io/raubaca/pen/obaZmG -->
                <div class="center-on-page">
                    <div class="pokeball">
                        <div class="pokeball__button"></div>
                    </div>
                </div>
            </div>

            <!-- Render error message if issue GET request fails-->
            <div v-if="error">
                <h2>{{error}}</h2>   
            </div>

            <!-- Render if pokemon data found -->
            <div v-if="pokemonData">
                <router-link to="/">Go Back</router-link>
                <b-card :img-src="pokemonData.sprites.front_default"
                img-alt="PokemonIMG"

                header="featured"
                >
                    <template v-slot:header>
                        <h6 style="text-align:center;">{{pokemonData.name | capitalize}}</h6>
                        <h6 style="text-align:center;">#{{pokemonData.id}}</h6>

                        <div style="display:block; text-align: center;">
                            <span v-for="(type, id) in pokemonData.types" v-bind:class="type.type.name" v-bind:key="id" class="monster-type"> {{type.type.name}}</span>
                        </div>
                    </template>

                    <b-list-group flush>

                        <!-- Base Stats START -->
                        <b-list-group-item>

                            <h6>Base Stats</h6>
                            <div class="stats">
                                <!-- HP -->
                                <div class="b_stat">
                                    <span>HP</span>
                                    <b-progress :value="pokemonData.stats[5].base_stat" height="1.3rem" :max=255 show-value></b-progress>
                                </div>

                                <!-- Attack -->
                                <div class="b_stat">
                                    <span>Attack</span>
                                    <b-progress :value="pokemonData.stats[4].base_stat" height="1.3rem" :max=255 show-value></b-progress>
                                </div>

                                <!-- Defense -->
                                <div class="b_stat">
                                    <span>Defense</span>
                                    <b-progress :value="pokemonData.stats[3].base_stat" height="1.3rem" :max=255 show-value></b-progress>
                                </div>

                                <!-- Speed -->
                                <div class="b_stat">
                                    <span>Speed</span>
                                    <b-progress :value="pokemonData.stats[0].base_stat" height="1.3rem" :max=255 show-value></b-progress>
                                </div>

                                <!-- Special Attack -->
                                <div class="b_stat">
                                    <span>Sp Att</span>
                                    <b-progress :value="pokemonData.stats[2].base_stat" height="1.3rem" :max=255 show-value></b-progress>
                                </div>

                                <!-- Special Defense -->
                                <div class="b_stat">
                                    <span>Sp Def</span>
                                    <b-progress :value="pokemonData.stats[1].base_stat" height="1.3rem" :max=255 show-value></b-progress>
                                </div>
                            </div>

                        </b-list-group-item>
                        <!-- Base Stats END -->


                        <!-- Profile START -->
                        <b-list-group-item>
                            <h6>Profile</h6>

                            <div>
                                <strong>Weight:</strong>
                                <span> {{pokemonData.weight / 10}} kg</span>
                            </div>

                            <div>
                                <strong>Height:</strong>
                                <span> {{pokemonData.height / 10}} m</span>
                            </div>

                            <div>
                                <strong>Catch Rate:</strong>
                                <span> {{speciesData.capture_rate}}</span>
                            </div>

                            <div>
                                <strong>Gender Ratio:</strong>
                                <span v-if="speciesData.gender_rate >= 0"> 
                                    {{(speciesData.gender_rate / 8) * 100}}% &#9792; {{((8-speciesData.gender_rate) / 8) * 100}}% &#9794;
                                </span>
                                <span v-else>
                                    Genderless
                                </span>
                            </div>  

                            <div>
                                <strong>Egg Groups:</strong>
                                <span v-for="(item, id) in speciesData.egg_groups" v-bind:key="id">
                                     {{item.name | capitalize}} /
                                </span>
                            </div>

                            <div>
                                <strong>Hatch Steps:</strong>
                                <span> {{(speciesData.hatch_counter + 1) * 255}}</span>
                            </div>

                            <div>
                                <strong>Abilities:</strong>
                                <span v-for="(item, id) in pokemonData.abilities" v-bind:key="id">
                                     {{item.ability.name | capitalize}} /
                                </span>
                            </div>

                            <div>
                                <strong>EVs:</strong>
                                <span v-for="(item, id) in pokemonData.stats" v-bind:key="id">
                                    <span v-if="item.effort != 0"> {{item.effort}} {{item.stat.name | capitalize}} / </span>
                                </span>
                            </div>

                        </b-list-group-item>
                        <!-- Profile END -->

                        <!-- Type Defenses START -->
                        <b-list-group-item>
                            <h6>Type Defenses</h6>
                            
                            <b-container fluid>
                                <b-row>
                                    
                                    <b-col sm style="padding-left:0px;"> 
                                        <!-- 4x Multiplier -->
                                        <div v-for="(type, id) in quadDamage" v-bind:key="id">
                                            <span v-bind:class="type" class="monster-type weakness"> {{type}}</span>
                                            <span class="multiplier">4x</span>
                                        </div>
                                        <!-- 2x Multiplier -->
                                        <div v-for="(type, id) in doubleDamage" v-bind:key="id">
                                            <span v-bind:class="type" class="monster-type weakness"> {{type}}</span>
                                            <span class="multiplier">2x</span>
                                        </div>
                                    </b-col>
                                    
                                    
                                    <b-col sm style="padding-left:0px;">
                                        <!-- x.5 Multiplier -->
                                        <div v-for="(type, id) in halfDamage" v-bind:key="id">
                                            <span v-bind:class="type" class="monster-type weakness"> {{type}}</span>
                                            <span class="multiplier">.5x</span>
                                        </div>
                                        <!-- x.25 Multiplier -->
                                        <div v-for="(type, id) in quarterDamage" v-bind:key="id">
                                            <span v-bind:class="type" class="monster-type weakness"> {{type}}</span>
                                            <span class="multiplier">.25x</span>
                                        </div>
                                        <!-- x0 Multiplier -->
                                        <div v-for="(type, id) in noDamage" v-bind:key="id">
                                            <span v-bind:class="type" class="monster-type weakness"> {{type}}</span>
                                            <span class="multiplier">0x</span>
                                        </div>
                                    </b-col>
                                </b-row>
                            </b-container>

                        </b-list-group-item>
                        <!-- Type Defenses END -->

                        <!-- Evolution Chart START -->
                        <b-list-group-item>

                            <h6>Evolution Chart</h6>
                            
                            <br/>
                            

                            <b-container style="text-align: center;" v-if="evolutionData.chain.evolves_to.length != 0">
                                <div><strong>1st Evolution Stage</strong></div>
                                <div v-for="(stage1, id) in evolutionData.chain.evolves_to" v-bind:key="id">
                                    <b-row>
                                        <b-col>
                                            <img v-on:click="pokemonRedirect(evolutionData.chain.species.name)" v-bind:src="getImage(evolutionData.chain.species.url)" alt="">
                                            
                                        </b-col>

                                        <b-col align-self="center">
                                            <img style="width:35%;" src="https://img.icons8.com/ios-filled/50/000000/arrow.png">
                                        </b-col>

                                        <b-col>
                                            <img v-on:click="pokemonRedirect(stage1.species.name)" v-bind:src="getImage(stage1.species.url)" alt="">
                                        </b-col>
                                    </b-row>

                                    <span>

                                        <strong v-if="evolutionData.chain.species.name == pokemonData.name">
                                            {{evolutionData.chain.species.name | capitalize}}
                                        </strong>
                                        <span v-else>
                                            {{evolutionData.chain.species.name | capitalize}}
                                        </span>

                                        evolves into 
                                            
                                        <strong v-if="stage1.species.name == pokemonData.name">
                                            {{stage1.species.name | capitalize}}
                                        </strong>
                                        <span v-else>
                                            {{stage1.species.name | capitalize}}
                                        </span>

                                        <span v-if="stage1.evolution_details.length != 0">

                                            <strong v-if="stage1.evolution_details[0].trigger">
                                                
                                                <!-- Level Up -->
                                                 <span v-if="stage1.evolution_details[0].trigger.name == 'level-up'">
                                                     by Leveling up

                                                     <!-- Level Condition -->
                                                     <span v-if="stage1.evolution_details[0].min_level != null">
                                                        to Lvl {{stage1.evolution_details[0].min_level}}
                                                     </span>

                                                     <!-- Stat Condition -->
                                                     <span v-if="stage1.evolution_details[0].relative_physical_stats != null">
                                                         <span v-if="stage1.evolution_details[0].relative_physical_stats == 1">
                                                             with Attack > Defense
                                                         </span>
                                                         <span v-if="stage1.evolution_details[0].relative_physical_stats == 0">
                                                             with Attack = Defense
                                                         </span>
                                                         <span v-if="stage1.evolution_details[0].relative_physical_stats == -1">
                                                             with Attack &#60; Defense
                                                         </span>
                                                     </span>

                                                     <!-- Happiness/Friendship Condition -->
                                                     <span v-if="stage1.evolution_details[0].min_happiness != null">
                                                         with {{stage1.evolution_details[0].min_happiness}} Happiness
                                                     </span>

                                                     <!-- Beauty Condition -->
                                                     <span v-if="stage1.evolution_details[0].min_beauty != null">
                                                         with {{stage1.evolution_details[0].min_beauty}} Beauty
                                                     </span>

                                                     <!-- Time of Day Condition -->
                                                     <span v-if="stage1.evolution_details[0].time_of_day != ''">
                                                         during {{stage1.evolution_details[0].time_of_day | capitalize}}
                                                    </span>

                                                    <!-- Location Condition -->
                                                    <span v-if="stage1.evolution_details[0].location != null">
                                                        at {{stage1.evolution_details[0].location.name | capitalize}}
                                                    </span>

                                                    <!-- Affection Condition -->
                                                    <span v-if="stage1.evolution_details[0].min_affection != null">
                                                        with 
                                                        <span v-for="index in stage1.evolution_details[0].min_affection" :key="index">
                                                            &hearts;
                                                        </span>
                                                        Affection in Pokémon-Amie
                                                    </span>

                                                    <span v-if="stage1.evolution_details[0].turn_upside_down == true">
                                                        while holding the Nintendo 3DS system upside-down
                                                    </span>

                                                    <!-- Held Item Condition -->
                                                     <span v-if="stage1.evolution_details[0].held_item != null">
                                                        while holding {{stage1.evolution_details[0].held_item.name | capitalize}}
                                                     </span>


                                                    <!-- Known Move Condition -->
                                                    <span v-if="stage1.evolution_details[0].known_move != null">
                                                        and learning {{stage1.evolution_details[0].known_move.name | capitalize}}
                                                    </span>

                                                    <!-- Gender Condition -->
                                                    <span v-if="stage1.evolution_details[0].gender != null">
                                                        (
                                                        <span v-if="stage1.evolution_details[0].gender == 1">
                                                            If Female
                                                        </span>
                                                        <span v-else>
                                                            If Male
                                                        </span>
                                                        )
                                                    </span>

                                                 </span>

                                                <!-- Use Item -->
                                                 <span v-else-if="stage1.evolution_details[0].trigger.name == 'use-item'">
                                                     <!-- Item -->
                                                     <span v-if="stage1.evolution_details[0].item != null">
                                                        by using {{stage1.evolution_details[0].item.name | capitalize}}
                                                     </span>

                                                     <!-- Happiness/Friendship Condition -->
                                                     <span v-if="stage1.evolution_details[0].min_happiness != null">
                                                         with {{stage1.evolution_details[0].min_happiness}} Happiness
                                                     </span>

                                                     <!-- Beauty Condition -->
                                                     <span v-if="stage1.evolution_details[0].min_beauty != null">
                                                         with {{stage1.evolution_details[0].min_beauty}} Beauty
                                                     </span>

                                                     <!-- Time of Day Condition -->
                                                     <span v-if="stage1.evolution_details[0].time_of_day != ''">
                                                         during {{stage1.evolution_details[0].time_of_day | capitalize}}
                                                    </span>

                                                    <!-- Location Condition -->
                                                    <span v-if="stage1.evolution_details[0].location != null">
                                                        at {{stage1.evolution_details[0].location.name | capitalize}}
                                                    </span>

                                                    <!-- Affection Condition -->
                                                    <span v-if="stage1.evolution_details[0].min_affection != null">
                                                        with 
                                                        <span v-for="index in stage1.evolution_details[0].min_affection" :key="index">
                                                            &hearts;
                                                        </span>
                                                        Affection in Pokémon-Amie
                                                    </span>

                                                     <!-- Gender Condition -->
                                                    <span v-if="stage1.evolution_details[0].gender != null">
                                                        (
                                                        <span v-if="stage1.evolution_details[0].gender == 1">
                                                            If Female
                                                        </span>
                                                        <span v-else>
                                                            If Male
                                                        </span>
                                                        )
                                                    </span>
                                                 </span>

                                                <!-- Trade -->
                                                 <span v-else-if="stage1.evolution_details[0].trigger.name == 'trade'">
                                                     by Trading

                                                    <!-- Trade for Certain Pokemon Condition -->
                                                    <span v-if="stage1.evolution_details[0].trade_species != null">
                                                         for a {{stage1.evolution_details[0].trade_species.name | capitalize}}
                                                    </span>

                                                     <!-- Trade Condition -->
                                                     <span v-if="stage1.evolution_details[0].held_item != null">
                                                        while holding {{stage1.evolution_details[0].held_item.name | capitalize}}
                                                     </span>

                                                     <!-- Happiness/Friendship Condition -->
                                                     <span v-if="stage1.evolution_details[0].min_happiness != null">
                                                         with {{stage1.evolution_details[0].min_happiness}} Happiness
                                                     </span>

                                                     <!-- Beauty Condition -->
                                                     <span v-if="stage1.evolution_details[0].min_beauty != null">
                                                         with {{stage1.evolution_details[0].min_beauty}} Beauty
                                                     </span>

                                                     <!-- Time of Day Condition -->
                                                     <span v-if="stage1.evolution_details[0].time_of_day != ''">
                                                         during {{stage1.evolution_details[0].time_of_day | capitalize}}
                                                    </span>

                                                    <!-- Location Condition -->
                                                    <span v-if="stage1.evolution_details[0].location != null">
                                                        at {{stage1.evolution_details[0].location.name | capitalize}}
                                                    </span>

                                                    <!-- Affection Condition -->
                                                    <span v-if="stage1.evolution_details[0].min_affection != null">
                                                        with 
                                                        <span v-for="index in stage1.evolution_details[0].min_affection" :key="index">
                                                            &hearts;
                                                        </span>
                                                        Affection in Pokémon-Amie
                                                    </span>

                                                    <!-- Gender Condition -->
                                                    <span v-if="stage1.evolution_details[0].gender != null">
                                                        (
                                                        <span v-if="stage1.evolution_details[0].gender == 1">
                                                            If Female
                                                        </span>
                                                        <span v-else>
                                                            If Male
                                                        </span>
                                                        )
                                                    </span>
                                                 </span>


                                            </strong>

                                        </span>


                                    </span>

                                    
                                    <div style="text-align: center;" v-if="evolutionData.chain.evolves_to[0].evolves_to.length != 0">
                                        <br/>
                                        <br/>
                                        <div><strong>2nd Evolution Stage</strong></div>
                                        <div v-for="(stage2, id2) in evolutionData.chain.evolves_to[0].evolves_to" v-bind:key="id2">
                                            <b-row fluid>
                                                <b-col align-self="center">
                                                    <b-img v-on:click="pokemonRedirect(stage1.species.name)" v-bind:src="getImage(stage1.species.url)" alt="">
                                                    </b-img>
                                                </b-col>

                                                <b-col align-self="center">
                                                    <b-img style="width:35%;" src="https://img.icons8.com/ios-filled/50/000000/arrow.png"></b-img>
                                                </b-col>

                                                <b-col align-self="center">
                                                    <b-img v-on:click="pokemonRedirect(stage2.species.name)" v-bind:src="getImage(stage2.species.url)" alt=""></b-img>
                                                </b-col>
                                            </b-row>

                                            <span>

                                        <strong v-if="stage1.species.name == pokemonData.name">
                                            {{stage1.species.name | capitalize}}
                                        </strong>
                                        <span v-else>
                                            {{stage1.species.name | capitalize}}
                                        </span>

                                        evolves into 
                                            
                                        <strong v-if="stage2.species.name == pokemonData.name">
                                            {{stage2.species.name | capitalize}}
                                        </strong>
                                        <span v-else>
                                            {{stage2.species.name | capitalize}}
                                        </span>

                                        <span v-if="stage2.evolution_details.length != 0">

                                            <strong v-if="stage2.evolution_details[0].trigger">
                                                
                                                <!-- Level Up -->
                                                 <span v-if="stage2.evolution_details[0].trigger.name == 'level-up'">
                                                     by Leveling up

                                                     <!-- Level Condition -->
                                                     <span v-if="stage2.evolution_details[0].min_level != null">
                                                        to Lvl {{stage2.evolution_details[0].min_level}}
                                                     </span>

                                                     <!-- Stat Condition -->
                                                     <span v-if="stage2.evolution_details[0].relative_physical_stats != null">
                                                         <span v-if="stage2.evolution_details[0].relative_physical_stats == 1">
                                                             with Attack > Defense
                                                         </span>
                                                         <span v-if="stage2.evolution_details[0].relative_physical_stats == 0">
                                                             with Attack = Defense
                                                         </span>
                                                         <span v-if="stage2.evolution_details[0].relative_physical_stats == -1">
                                                             with Attack &#60; Defense
                                                         </span>
                                                     </span>

                                                     <!-- Happiness/Friendship Condition -->
                                                     <span v-if="stage2.evolution_details[0].min_happiness != null">
                                                         with {{stage2.evolution_details[0].min_happiness}} Happiness
                                                     </span>

                                                     <!-- Beauty Condition -->
                                                     <span v-if="stage2.evolution_details[0].min_beauty != null">
                                                         with {{stage2.evolution_details[0].min_beauty}} Beauty
                                                     </span>

                                                     <!-- Time of Day Condition -->
                                                     <span v-if="stage2.evolution_details[0].time_of_day != ''">
                                                         during {{stage2.evolution_details[0].time_of_day | capitalize}}
                                                    </span>

                                                    <!-- Location Condition -->
                                                    <span v-if="stage2.evolution_details[0].location != null">
                                                        at {{stage2.evolution_details[0].location.name | capitalize}}
                                                    </span>

                                                    <!-- Affection Condition -->
                                                    <span v-if="stage2.evolution_details[0].min_affection != null">
                                                        with 
                                                        <span v-for="index in stage2.evolution_details[0].min_affection" :key="index">
                                                            &hearts;
                                                        </span>
                                                        Affection in Pokémon-Amie
                                                    </span>

                                                    <!-- Trade Condition -->
                                                     <span v-if="stage2.evolution_details[0].held_item != null">
                                                        while holding {{stage2.evolution_details[0].held_item.name | capitalize}}
                                                     </span>

                                                    <!-- Known Move Condition -->
                                                    <span v-if="stage2.evolution_details[0].known_move != null">
                                                        and learning {{stage2.evolution_details[0].known_move.name | capitalize}}
                                                    </span>

                                                    <!-- Gender Condition -->
                                                    <span v-if="stage2.evolution_details[0].gender != null">
                                                        (
                                                        <span v-if="stage2.evolution_details[0].gender == 1">
                                                            If Female
                                                        </span>
                                                        <span v-else>
                                                            If Male
                                                        </span>
                                                        )
                                                    </span>

                                                 </span>

                                                <!-- Use Item -->
                                                 <span v-else-if="stage2.evolution_details[0].trigger.name == 'use-item'">
                                                     <!-- Item -->
                                                     <span v-if="stage2.evolution_details[0].item != null">
                                                        by using {{stage2.evolution_details[0].item.name | capitalize}}
                                                     </span>

                                                     <!-- Happiness/Friendship Condition -->
                                                     <span v-if="stage2.evolution_details[0].min_happiness != null">
                                                         with {{stage2.evolution_details[0].min_happiness}} Happiness
                                                     </span>

                                                     <!-- Beauty Condition -->
                                                     <span v-if="stage2.evolution_details[0].min_beauty != null">
                                                         with {{stage2.evolution_details[0].min_beauty}} Beauty
                                                     </span>

                                                     <!-- Time of Day Condition -->
                                                     <span v-if="stage2.evolution_details[0].time_of_day != ''">
                                                         during {{stage2.evolution_details[0].time_of_day | capitalize}}
                                                    </span>

                                                    <!-- Location Condition -->
                                                    <span v-if="stage2.evolution_details[0].location != null">
                                                        at {{stage2.evolution_details[0].location.name | capitalize}}
                                                    </span>

                                                    <!-- Affection Condition -->
                                                    <span v-if="stage2.evolution_details[0].min_affection != null">
                                                        with 
                                                        <span v-for="index in stage2.evolution_details[0].min_affection" :key="index">
                                                            &hearts;
                                                        </span>
                                                        Affection in Pokémon-Amie
                                                    </span>

                                                     <!-- Gender Condition -->
                                                    <span v-if="stage2.evolution_details[0].gender != null">
                                                        (
                                                        <span v-if="stage2.evolution_details[0].gender == 1">
                                                            If Female
                                                        </span>
                                                        <span v-else>
                                                            If Male
                                                        </span>
                                                        )
                                                    </span>
                                                 </span>

                                                <!-- Trade -->
                                                 <span v-else-if="stage2.evolution_details[0].trigger.name == 'trade'">
                                                     by Trading

                                                     <!-- Trade for Certain Pokemon Condition -->
                                                    <span v-if="stage1.evolution_details[0].trade_species != null">
                                                         for a {{stage1.evolution_details[0].trade_species.name | capitalize}}
                                                    </span>

                                                     <!-- Trade Condition -->
                                                     <span v-if="stage2.evolution_details[0].held_item != null">
                                                        while holding {{stage2.evolution_details[0].held_item.name | capitalize}}
                                                     </span>

                                                     <!-- Happiness/Friendship Condition -->
                                                     <span v-if="stage2.evolution_details[0].min_happiness != null">
                                                         with {{stage2.evolution_details[0].min_happiness}} Happiness
                                                     </span>

                                                     <!-- Beauty Condition -->
                                                     <span v-if="stage2.evolution_details[0].min_beauty != null">
                                                         with {{stage2.evolution_details[0].min_beauty}} Beauty
                                                     </span>

                                                     <!-- Time of Day Condition -->
                                                     <span v-if="stage2.evolution_details[0].time_of_day != ''">
                                                         during {{stage2.evolution_details[0].time_of_day | capitalize}}
                                                    </span>

                                                    <!-- Location Condition -->
                                                    <span v-if="stage2.evolution_details[0].location != null">
                                                        at {{stage2.evolution_details[0].location.name | capitalize}}
                                                    </span>

                                                    <!-- Affection Condition -->
                                                    <span v-if="stage2.evolution_details[0].min_affection != null">
                                                        with 
                                                        <span v-for="index in stage2.evolution_details[0].min_affection" :key="index">
                                                            &hearts;
                                                        </span>
                                                        Affection in Pokémon-Amie
                                                    </span>

                                                    <!-- Gender Condition -->
                                                    <span v-if="stage2.evolution_details[0].gender != null">
                                                        (
                                                        <span v-if="stage2.evolution_details[0].gender == 1">
                                                            If Female
                                                        </span>
                                                        <span v-else>
                                                            If Male
                                                        </span>
                                                        )
                                                    </span>
                                                 </span>


                                            </strong>

                                        </span>


                                    </span>
                                        </div>

                                        
                                    </div>

                                </div>

                            </b-container>

                            <b-container style="text-align: center;" v-else>
                                <strong>No evolution stages</strong>
                            </b-container>


                            <br/>
                        </b-list-group-item>
                        <!-- Evolution Chart END -->

                    </b-list-group>



                </b-card>
            </div>
        </section>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'Pokemon',
    data() {
        return {
            loading: false,
            error: null,
            pokemonData: null,
            speciesData: null,
            primaryTypeData: null,
            secondaryTypeData: null,
            evolutionData: null,
            
            quadDamage: [],
            doubleDamage: [],
            halfDamage: [],
            quarterDamage: [],
            noDamage: [],

            evolutionChain: []
        };
    },

    async created() {
        this.loading = true;

        try {

            const res = await axios.get(`/pokemon/${this.$route.params.pokemon}`);

            this.pokemonData = res.data;
            //console.log(this.pokemonData);

            const res2 = await axios.get(this.pokemonData.species.url);

            this.speciesData = res2.data;
            //console.log(this.speciesData);

            const res3 = await axios.get(this.pokemonData.types[0].type.url);

            this.primaryTypeData = res3.data;

        
            this.addDoubleDamage(this.primaryTypeData.damage_relations.double_damage_from);
            this.addHalfDamage(this.primaryTypeData.damage_relations.half_damage_from);
            this.addNoDamage(this.primaryTypeData.damage_relations.no_damage_from);

            if(this.pokemonData.types[1] != undefined){
                const res4 = await axios.get(this.pokemonData.types[1].type.url)

                this.secondaryTypeData = res4.data;
                this.addDoubleDamage(this.secondaryTypeData.damage_relations.double_damage_from);
                this.addHalfDamage(this.secondaryTypeData.damage_relations.half_damage_from);
                this.addNoDamage(this.secondaryTypeData.damage_relations.no_damage_from);
                
            }
            this.checkSingleDamage();
            this.checkNoDamage();

            const res5 = await axios.get(this.speciesData.evolution_chain.url);

            this.evolutionData = res5.data;
            

        }
        catch(err) {

            this.loading = false;
            this.error = err.response.data.message;
            
        }
    },

    methods: {
        addDoubleDamage: function(typeArr) 
        {
            typeArr.forEach(type => {
                if(this.doubleDamage.find(item => {return item == type.name}) != undefined){
                    var index = this.doubleDamage.indexOf(type.name);
                    if(index > -1){
                        this.doubleDamage.splice(index, 1);
                        this.quadDamage.push(type.name);
                    }
                }
                else{
                    this.doubleDamage.push(type.name);
                }
            });
        },
        
        addHalfDamage: function(typeArr)
        {
            typeArr.forEach(type => {
                if(this.halfDamage.find(item => {return item == type.name}) != undefined){
                    var index = this.halfDamage.indexOf(type.name);
                    if(index > -1){
                        this.halfDamage.splice(index, 1);
                        this.quarterDamage.push(type.name);
                    }
                }
                else{
                    this.halfDamage.push(type.name);
                }
            });
        },

        addNoDamage: function(typeArr) {
            typeArr.forEach(type => {
                if(this.noDamage.find(item => {return item == type.name}) == undefined){
                    this.noDamage.push(type.name);
                }
            });
        },

        checkSingleDamage: function ()
        {
           let i = this.halfDamage.length;
           while(i--){
               
               let type = this.halfDamage[i];
               if(this.doubleDamage.find(item => {return item == type}) != undefined){
                    
                    var index = this.doubleDamage.indexOf(type);

                    this.halfDamage.splice(i, 1);

                    if(index > -1){
                        this.doubleDamage.splice(index, 1);
                    }
                    
               }
               
           }
            //console.log('halfDamage: ' + this.halfDamage);
            //console.log('doubleDamage: ' + this.doubleDamage);
            
        },

        checkNoDamage: function() {
            this.noDamage.forEach(type => {
                var index;
                if(this.quadDamage.find(item => {return item == type}) != undefined){
                    index = this.quadDamage.indexOf(type);
                    if(index > -1){
                        this.quadDamage.splice(index, 1)
                    }
                }

                if(this.doubleDamage.find(item => {return item == type}) != undefined){
                    index = this.doubleDamage.indexOf(type);
                    if(index > -1){
                        this.doubleDamage.splice(index, 1)
                    }
                }

                if(this.halfDamage.find(item => {return item == type}) != undefined){
                    index = this.halfDamage.indexOf(type);
                    if(index > -1){
                        this.halfDamage.splice(index, 1)
                    }
                }

                if(this.quarterDamage.find(item => {return item == type}) != undefined){
                    index = this.quarterDamage.indexOf(type);
                    if(index > -1){
                        this.quarterDamage.splice(index, 1)
                    }
                }
            })
        },
        getImage: function(url) {
            var newUrl = (url).replace('https://pokeapi.co/api/v2/pokemon-species/','https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/');

            newUrl = newUrl.replace(/.$/, '.png');

            //console.log(newUrl);
            return newUrl;
        },

        pokemonRedirect: function(pokemonName){
            this.$router.push(`/pokemon/` + pokemonName);
        }
    },

    filters: {
        capitalize: function (value) {
            if(!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        },

        
    }

}
</script>

